.header {
    background-color: #ffffff;
    padding: 20px;
    .toggle-sidebar {
        cursor: pointer;
    }
    .notifications-container {
        display: inline-block;
        margin-left: 20px;
        .toggle-notifications#notifications {
            background-color: #f9f9f9;
            padding: 10px;
            border-radius: 50%;
            color: #555555;
            cursor: pointer;
        }
        .dropdown-menu {
            min-width: 600px;
            border: none;
            padding: 0;
            top: 10px !important;
            &:before {
                content: '';
                position: absolute;
                top: -20px;
                left: 10px;
                border-style: solid;
                border-width: 10px;
                border-color: transparent transparent #333333 transparent;
            }
            .noti-head {
                background-color: #333333;
                padding: 15px;
                color: #ffffff;
                span {
                    float: right;
                }
            }
            .noti-content {
                padding: 20px;
                ul {
                    margin: 0;
                    li {
                        padding: 10px;
                        i{
                            background-color: #eeeeee;
                            padding: 10px;
                            border-radius: 50px;
                            width: 45px;
                            height: 45px;
                            line-height: 26px;
                            text-align: center;
                            margin-right: 10px;
                            position: relative;
                            top: -2px;
                        }
                    }
                }
            }
            .noti-foot {
                a {
                    display: block;
                    padding: 10px;
                    border-top: 1px solid #eeeeee;
                    &:hover {
                        text-decoration: none;
                    }
                }
            }
        }
    }
}